import React from 'react';
import cn from 'classnames';

import styles from './style.module.css';

export default ({ decorator }: any) => {
  const VideoComponent = ({ blockProps }: { blockProps: any }) => {
    const { url, isFocused } = blockProps;

    return (
      <div className={styles.container}>
        <div className={styles.inner}>
          <div className={styles.videoContainer}>
            <iframe
              className={cn(styles.video, { [styles.active]: isFocused })}
              src={url}
              frameBorder="0"
              allowFullScreen
            />
          </div>
        </div>
      </div>
    );
  };

  return decorator ? decorator(VideoComponent) : VideoComponent;
};
