import { Entity } from 'draft-js';
// @ts-ignore-next-line
import { removeBlock, setEntityData } from 'src/shared/utils/draftjs';

// TODO: Убедиться в необходимости этого плагина (судя по всему раньше в API не было метода .getData() и .mergeData())
export default (config: any = {}) => {
  return {
    blockRendererFn: (contentBlock: any, pluginEditor: any) => {
      const entityKey = contentBlock.getEntityAt(0);
      if (!entityKey) return;
      const entityObject = Entity.get(entityKey);
      const entityData = entityObject.getData();

      return {
        props: {
          pluginEditor: pluginEditor,
          entityData: entityData,
          setEntityData: contentBlock && setEntityData(contentBlock, pluginEditor),
          removeBlock: pluginEditor.editorState && contentBlock &&
            removeBlock(pluginEditor.editorState, contentBlock.getKey())
        }
      };
    },
  };
};