// @ts-ignore-next-line
import removeEntitiesAtEdges from 'draft-js/lib/removeEntitiesAtEdges';
// @ts-ignore-next-line
import removeRangeFromContentState from 'draft-js/lib/removeRangeFromContentState';

import insertFragmentIntoContentState from './insertFragmentIntoContentState';

export default (contentState: any, targetRange: any, fragment: any) => {
  const withoutEntities = removeEntitiesAtEdges(contentState, targetRange);
  const withoutText = removeRangeFromContentState(
    withoutEntities,
    targetRange,
  );

  return insertFragmentIntoContentState(
    withoutText,
    withoutText.getSelectionAfter(),
    fragment,
  );
};
