const BotPanel = ({
  context,
  editorState,
  setContext,
  setFocus,
  applyModifier,
}: any) => {
  const PanelComponent = (context && PANELS_CONTEXT_MAP[context.type]) || DefaultPanel;
  console.log(context && context.type);
  return (
    <div className={styles.wrapper}>
      <div className={styles.inner}>
        <PanelComponent
          context={context}
          editorState={editorState}
          setFocus={setFocus}
          applyModifier={applyModifier}
          setContext={setContext}
        />
      </div>
    </div>
  );
};

export default BotPanel;
