import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { Entity } from 'draft-js';

const getLinkComponent = ({ decorator }: any) => {
  class LinkEntity extends Component<any, any> {

    constructor(props: any) {
      super(props);

      this.state = {
        showPopOver: false,
      };
    }

    openLink() {
      const { entityKey } = this.props;
      const { url } = Entity.get(entityKey).getData();
      const linkTab: any = window.open(url, 'blank'); // eslint-disable-line no-undef
      linkTab.focus();
    }

    render() {
      const { children, entityKey } = this.props;
      const { url } = Entity.get(entityKey).getData();

      return (
        <span
          className="itd-link-decorator-wrapper"
        >
          <a href={url}>{children}</a>
        </span>
      );
    }
  }

  // LinkEntity.propTypes = {
  //   entityKey: PropTypes.string.isRequired,
  // };

  return decorator ? decorator(LinkEntity) : LinkEntity;
};

export default getLinkComponent;
