import cn from 'classnames';
import React, { Component } from 'react';

import { Entity } from 'draft-js';
import { getSelectionEntity } from 'src/shared/utils/draftjs';

import { addLink, removeLink } from 'src/Editor/modifiers';

import styles from './editorLink.module.css';

const preventDefault = (e: React.MouseEvent) => {
  e.preventDefault();
  e.stopPropagation();
};

class EditorLinkPanel extends Component<any, any> {
  urlInput: any;

  constructor(props: any) {
    super(props);

    const { editorState } = props;

    const currentEntityKey = getSelectionEntity(editorState);
    const currentEntity: any = currentEntityKey ? Entity.get(currentEntityKey) : null;
    const hasLinkEntity = currentEntity && currentEntity.get('type') === 'LINK';

    const url = hasLinkEntity ? currentEntity.data.url : '';

    this.state = {
      url,
      hasLinkEntity,
    };
  }

  componentDidMount() {
    const { forceFocus } = this.props;

    if (forceFocus) {
      setTimeout(() => this.urlInput.focus(), 50);
    }
  }

  componentWillReceiveProps(nextProps: any) {
    const { editorState } = nextProps;

    const currentEntityKey = getSelectionEntity(editorState);
    const currentEntity: any = currentEntityKey ? Entity.get(currentEntityKey) : null;
    const hasLinkEntity = currentEntity && currentEntity.getType() === 'LINK';

    const url = hasLinkEntity ? currentEntity.data.url : '';

    this.setState({ hasLinkEntity, url });
  }

  addLink = (e: React.MouseEvent) => {
    preventDefault(e);

    this.props.applyModifier(addLink, { url: this.state.url });
  };

  removeLink = (e: React.MouseEvent) => {
    preventDefault(e);

    this.props.applyModifier(removeLink);
  };

  cancel = (e: React.MouseEvent) => {
    preventDefault(e);
    this.props.setContext(null);
  };

  render() {
    const { url, hasLinkEntity } = this.state;

    return (
      <div className={styles.wrapper}>
        <div className={styles.leftButtons}>
          <input
            className="input"
            ref={(ref) => {
              this.urlInput = ref;
            }}
            type="text"
            value={url}
            onChange={e => this.setState({ url: e.target.value })}
            placeholder="http://"
          />
        </div>

        <div className={styles.centerButtons}>
          <button
            className={cn(styles.button, styles.active)}
            onClick={this.addLink}
          >
            OK
          </button>


          {
            hasLinkEntity ? (
              <button
                className={styles.button}
                onClick={this.removeLink}
              >
                Remove
              </button>
            ) : (
              <button
                className={styles.button}
                onClick={this.cancel}
              >
                Cancel
              </button>
            )
          }
        </div>
      </div>
    );
  }
}

export default EditorLinkPanel;
