import {
  EditorState,
  Modifier,
  ContentBlock,
  BlockMapBuilder,
  genKey,
} from 'draft-js';
import { List } from 'immutable';

import replaceWithFragment from './replaceWithFragment';

export default (editorState: any, type: any, data: any, key = null) => {
  const contentState = editorState.getCurrentContent();
  const selectionState = editorState.getSelection();

  const afterRemoval = Modifier.removeRange(contentState, selectionState, 'backward');

  const targetSelection = afterRemoval.getSelectionAfter();
  const afterSplit = Modifier.splitBlock(afterRemoval, targetSelection);
  const insertionTarget = afterSplit.getSelectionAfter();

  const asAtomicBlock = Modifier.setBlockType(afterSplit, insertionTarget, type);

  const blockKey = key || genKey();

  const customBlock = new ContentBlock({
    key: blockKey,
    type,
    text: '',
    data,
  });
  const unstyledBlock = new ContentBlock({
    key: genKey(),
    type: 'unstyled',
    text: '',
    characterList: List(),
  });

  const fragmentArray = [customBlock, unstyledBlock];

  const fragment = BlockMapBuilder.createFromArray(fragmentArray);

  const withAtomicBlock = replaceWithFragment(asAtomicBlock, insertionTarget, fragment);

  const newContent = withAtomicBlock.merge({
    selectionBefore: selectionState,
    selectionAfter: withAtomicBlock.getSelectionAfter().set('hasFocus', true),
  });

  return EditorState.push(editorState, newContent, 'insert-fragment');
};
