import { EditorState, RichUtils, SelectionState } from 'draft-js';
import { getSelectedBlock } from "src/shared/utils/draftjs";
import { ModifierStateCallbacks } from "src/Editor/types";

export const applyBlockSeqStyles = (
  { setEditorState }: ModifierStateCallbacks,
  selectionSnapshot: SelectionState,
  { sequence = [], fallback = 'unstyled' }: any,
) =>
  setEditorState((editorState: EditorState) => {
    const tempEditorState = EditorState.acceptSelection(editorState, selectionSnapshot);
    const block = getSelectedBlock(tempEditorState);

    if (!block) return editorState;

    const blockType = block.getType();
    const currentTypePos = sequence.indexOf(blockType);

    const newBlockType = sequence.length && currentTypePos < sequence.length
      ? sequence[currentTypePos + 1]
      : fallback;

    return RichUtils.toggleBlockType(editorState, newBlockType);
  });

export default {
  applyBlockSeqStyles,
};
