import React, { useCallback } from 'react';

import { applyBlockStyle } from 'src/Editor/modifiers';

export const EditorHeadersPanel = ({ applyModifier }: any) => {
  const headerOne = useCallback(() =>
    applyModifier(applyBlockStyle, { style: 'header-one' }), [applyModifier]);
  const headerTwo = useCallback(() =>
    applyModifier(applyBlockStyle, { style: 'header-two' }), [applyModifier]);
  return (
    <div
      onMouseDown={(e) => {
        e.preventDefault();
        e.stopPropagation();
      }}
    >
      <button
        onClick={headerOne}
      >
        H1
      </button>

      <button
        onClick={headerTwo}
      >
        H2
      </button>
    </div>
  );
};

export default EditorHeadersPanel;
