import React, {useCallback} from 'react';

import { applyImageStyle } from 'src/Editor/modifiers';

export const CONTEXT_TYPE = 'editor-image';

export default ({ applyModifier }: any) => {
  const setStyleToSide = useCallback(() =>
    applyModifier(applyImageStyle, 'aside'), [applyModifier]);
  const setStyleToFull = useCallback(() =>
    applyModifier(applyImageStyle, 'full'), [applyModifier]);
  const setStyleToText = useCallback(() =>
    applyModifier(applyImageStyle, 'byText'), [applyModifier]);
  const setStyleToContent = useCallback(() =>
    applyModifier(applyImageStyle, 'byContent'), [applyModifier]);

  return (
    <div
      onMouseDown={(e) => {
        e.preventDefault();
        e.stopPropagation();
      }}
    >
      <button
        onClick={setStyleToSide}
      >
        Left
      </button>

      <button
        onClick={setStyleToFull}
      >
        Full
      </button>

      <button
        onClick={setStyleToText}
      >
        By text
      </button>

      <button
        onClick={setStyleToContent}
      >
        By content
      </button>
    </div>
  );
};
