import React, { Component, useCallback, useEffect } from 'react';
import { ContentBlock } from 'draft-js';
import cn from 'classnames';
import { BlockKeyStore } from '../../plugins/innerFocus/utils/createBlockKeyStore';
import { determineIfIsWrappedObject, PluginWrappedComponent } from '../../../shared/types/pluginDecorator';

// Get a component's display name
const getDisplayName = (wrappedComponent: PluginWrappedComponent) => {
  const component: any = wrappedComponent.WrappedComponent || wrappedComponent;
  return determineIfIsWrappedObject(component) ? component.displayName : component.name || 'Component';
};

export type BlockFocusDecoratorProps = {
  theme: { [K in string]: string },
  blockKeyStore: BlockKeyStore,
}

export type BlockFocusWrappedComponentProps = {
  block: ContentBlock,
  className?: string,
  blockProps: {
    isFocused: boolean,
    setFocusToBlock: () => void,
  }
}

export default ({ theme, blockKeyStore }: BlockFocusDecoratorProps) =>
  (WrappedComponent: PluginWrappedComponent) => {
    return (props: BlockFocusWrappedComponentProps) => {
      const { blockProps, className } = props;
      const { isFocused } = blockProps;
      const combinedClassName = cn(className, isFocused ? theme.focused : theme.unfocused);

      useEffect(() => {
        blockKeyStore.add(props.block.getKey());

        return () => {
          blockKeyStore.remove(props.block.getKey());
        };
      }, [props.block]);

      const onClick = useCallback((e: React.MouseEvent) => {
        e.preventDefault();
        if (!props.blockProps.isFocused) {
          props.blockProps.setFocusToBlock();
        }
      }, [props.blockProps.isFocused, props.blockProps.setFocusToBlock]);

      return (
        <WrappedComponent
          {...props}
          onClick={onClick}
          className={combinedClassName}
        />
      );
    };
  };
