import React from 'react';
import cn from 'classnames';
import styles from './style.module.css';

const getImageComponent = ({ decorator }: any) => {
  const ImageComponent = (props: any) => {
    const { url, style = 'byContent', isFocused, setFocusToBlock } = props.blockProps;

    return (
      <div
        onClick={(e) => {
          e.stopPropagation();
          e.preventDefault();
          if (!isFocused) {
            setFocusToBlock();
          }
        }}
        className={cn(styles.container, styles[style])}
      >
        <img
          src={url}
          alt=""
          className={cn(styles.image, { [styles.active]: isFocused })}
        />
      </div>
    );
  };
  return decorator ? decorator(ImageComponent) : ImageComponent;
};

export default getImageComponent;
