import creator from './components/embedded';
import { EditorContextType } from "src/Editor/types";

const embeddedPlugin = (config: any = {}) => {
  const component = creator(config);
  return {
    blockRendererFn: (contentBlock: any, { getProps }: any) => {
      if (contentBlock.getType() !== EditorContextType.EMBED) {
        return;
      }

      const { url, style } = (contentBlock.data ? contentBlock.data.toObject() : {}) as any;

      return {
        component,
        editable: false,
        props: {
          url,
          style,
        },
      };
    },
  };
};

export default embeddedPlugin;

export const embeddedTheme = {};
export const embeddedCreator = creator;
