import React, { useCallback, useState, useEffect, useRef } from 'react';

import {
  EditorState,
  convertFromRaw,
  convertToRaw,
  SelectionState,
  Entity,
  RichUtils,
  RawDraftContentState,
} from 'draft-js';

import PluginsEditor, { EditorPlugin } from 'draft-js-plugins-editor';

import { getSelectionEntity, getSelectedBlock } from 'src/shared/utils/draftjs';

import './editor.css';

import getCustomStyleMap from './stylesMap';
import { blockStyleFn, blockRendererFn, blockRenderMap } from './blocks';

import { EditorContextType, EditorModifier, EditorModifierWrapper, EditorStateSetter } from './types';

const customStyleMap = getCustomStyleMap();

type EditorProps = {
  readOnly: boolean,
  isPresentation: boolean,
  files: Array<Blob>,
  plugins?: EditorPlugin[],
  context: any,
  clearContext: any;
  onStateChange: (rawEditorState: EditorState) => void;
  onContextChange: (contextType: EditorContextType | null, meta?: any) => void,
  editorState: EditorState,
};

const Editor = (props: EditorProps) => {
  const editor = useRef<PluginsEditor>(null);

  // Editor state
  const editorState = props.editorState || EditorState.createEmpty();

  const lastFocus = useRef<number>(Date.now());
  const lastUpdate = useRef<number>(Date.now());

  const { readOnly, onContextChange, onStateChange, plugins, files } = props;

  // Handlers
  const onFocus = useCallback(() => {
    editor.current && editor.current.getPluginMethods().setReadOnly(false);

    lastUpdate.current = Date.now();

    setTimeout(() => {
      console.info('!!!! FOCUS !!!!');
      // editor.current && editor.current.focus()
    });
  }, [editor]);

  const onBlur = useCallback(() => {
    // TODO: Проверить кейс
    // props.onContextChange(null);
  }, [onContextChange]);

  // TODO: Добавить debounce 100ms?
  const setContext = useCallback((ongoingEditorState: EditorState) => {
    const ongoingSelectionState = ongoingEditorState.getSelection();

    if (!ongoingSelectionState.getHasFocus()) {
      return;
    }

    const currentBlock = getSelectedBlock(ongoingEditorState);
    const currentBlockType = currentBlock && currentBlock.getType();
    const currentEntityKey = getSelectionEntity(ongoingEditorState);
    const currentEntityType = currentEntityKey ? Entity.get(currentEntityKey).getType() : null;

    let ignoreBlur: boolean = false;

    console.log({ currentBlockType });

    if (currentBlockType === 'header-one' || currentBlockType === 'header-two') {
      onContextChange(EditorContextType.TEXT);
    } else if (currentBlockType === 'quote-one' || currentBlockType === 'quote-two') {
      onContextChange(EditorContextType.TEXT);
    } else if (currentEntityType === EditorContextType.LINK) {
      ignoreBlur = true;
      onContextChange(EditorContextType.LINK);
    } else if (currentBlockType === EditorContextType.IMAGE) {
      ignoreBlur = true;
      onContextChange(EditorContextType.IMAGE);
    } else if (currentBlockType === EditorContextType.VIDEO) {
      ignoreBlur = true;
      onContextChange(EditorContextType.VIDEO);
    } else if (currentBlockType === EditorContextType.EMBED) {
      ignoreBlur = true;
      onContextChange(EditorContextType.EMBED);
    } else {
      const isSelectionCollapsed = ongoingSelectionState.isCollapsed();
      const contextType = isSelectionCollapsed ? EditorContextType.BLOCK : EditorContextType.TEXT;

      onContextChange(contextType);
    }

    // TODO: Проверить актуальность этого подхода
    if (!ignoreBlur) {
      lastFocus.current = Date.now();
    }
  }, [onContextChange]);

  const onChange = useCallback((ongoingEditorState: EditorState) => {
    // TODO: Condition?
    setContext(ongoingEditorState);
    onStateChange(ongoingEditorState);
  }, [onStateChange, onFocus, setContext]);

  const handleKeyCommand = useCallback((command, editorState) => {
    const newState = RichUtils.handleKeyCommand(editorState, command);
    console.log('handleKeyCommand?', command);
    if (newState) {
      onChange(newState);
      return 'handled';
    }

    return 'not-handled';
  }, [onChange]);

  return (
    <div
      onClick={onFocus}
    >
      {/*TODO: Разобраться с пробрасыванием значений*/}
      <PluginsEditor
        // @ts-ignore
        files={files}
        placeholder="New cool title here"
        editorState={editorState}
        onBlur={onBlur}
        onChange={onChange}
        plugins={plugins}
        readOnly={readOnly}
        customStyleMap={customStyleMap}
        blockStyleFn={blockStyleFn}
        blockRenderMap={blockRenderMap}
        blockRendererFn={blockRendererFn}
        handleKeyCommand={handleKeyCommand}
        ref={editor}
      />
    </div>
  );
};

export type ApplyModifier = (modifier: EditorModifierWrapper, meta?: EditorState | Object) => void;

/*
* FIXME:
*  1. Изменение selection state приводит к обновлению debounce time, что в принципе не совсем верно
*       (но это не точно)
*  2. Множественные ре-ренды при инициализации и смене фокуса
*       (при изменении контента в целом всё ок)
* */

export const useEditor = (initialRawState: RawDraftContentState, debounceTime: number = 1500) => {
  const modifiersStack = useRef<EditorStateSetter[]>([]);
  const [exportedEditorState, setExportedEditorState] = useState<EditorState>(
    !initialRawState
      ? EditorState.createEmpty()
      : EditorState.createWithContent(convertFromRaw(initialRawState))
  );
  const rawState = useRef<RawDraftContentState>(initialRawState);
  const applyModifiersTimer = useRef<number>();
  const lastUpdateTimestamp = useRef<number>(0);
  /*
  * ВНИМАНИЕ: Для чтения использовать ТОЛЬКО liveEditorState, сам exportedEditorState нужен только для записи и ре-рендера.
  * Эта копия стейта нам нужна для внутренних обращений внутри различных useCallback функций, которые гипотетически
  * могут оказаться скопированы в асинхронные функции (например в setTimeout) и получить устаревший слепок данных.
  * Подход с `setState(state => state)` здесь не подходит, т.к. там не рекомендуется использовать сайд-эффеекты, а
  * таковые тут присутствуют, например в виде стека модификаторов. К тому же из-за Strict Mode в девелопе это ломает
  * работу таких функций вообще (из-за двойного вызова), приходится использовать костыли с замыканием.
  * */
  const liveEditorState = useRef<EditorState>(exportedEditorState);

  const updateEditorState = useCallback((newEditorState: EditorState) => {
    liveEditorState.current = newEditorState;
    setExportedEditorState(newEditorState);
  }, []);

  const runApplyingModifiers = useCallback(() => {
    if (modifiersStack.current.length) {
      // Применяем по очереди все накопленные модификаторы
      const modifiedEditorState = modifiersStack.current
        .reduce((accEditorState, modifier) =>
          modifier(accEditorState), liveEditorState.current);
      // Это нужно, чтобы вернуть в прежнее состояние курсор пользователя
      const newEditorState = EditorState.acceptSelection(modifiedEditorState, liveEditorState.current.getSelection());

      modifiersStack.current = [];
      updateEditorState(newEditorState);
    }
  }, [updateEditorState]);

  const reRunModifiersDebounceTimer = useCallback(() => {
    window.clearTimeout(applyModifiersTimer.current);
    applyModifiersTimer.current = window.setTimeout(runApplyingModifiers, debounceTime);
  }, [debounceTime, runApplyingModifiers]);

  const getEditorState = useCallback(() => liveEditorState.current, []);
  const setEditorState = useCallback((modifier: EditorStateSetter, immediately?: boolean) => {
    const timestampNow = Date.now();

    if (immediately) {
      const newEditorState = modifier(liveEditorState.current);

      lastUpdateTimestamp.current = timestampNow;

      reRunModifiersDebounceTimer();
      updateEditorState(newEditorState);
    } else {
      modifiersStack.current = [
        ...modifiersStack.current,
        modifier,
      ];

      if (timestampNow - lastUpdateTimestamp.current >= debounceTime) {
        runApplyingModifiers();
      }
    }
  }, [exportedEditorState, reRunModifiersDebounceTimer, updateEditorState, debounceTime, runApplyingModifiers]);

  const applyModifier = useCallback((modifier: EditorModifierWrapper, meta?: EditorState | Object) => {
    const selectionSnapshot = liveEditorState.current.getSelection();

    modifier({ getEditorState, setEditorState }, selectionSnapshot, meta);
  }, [getEditorState, setEditorState]);

  /* Обновляем состояние если обновление прибыло "из вне" и мы о нём не знаем */
  useEffect(() => {
    if (initialRawState !== rawState.current) {
      rawState.current = initialRawState;
      setExportedEditorState(EditorState.createWithContent(convertFromRaw(initialRawState)));
    }
  }, [initialRawState]);

  /* Обновляем локальное "raw" состояние если "draft" состояние было изменено */
  useEffect(() => {
    rawState.current = convertToRaw(exportedEditorState.getCurrentContent());
  }, [exportedEditorState]);

  // TODO: Посмотреть почему оно не хавает экспорт массива
  return {
    applyModifier,
    editorState: exportedEditorState,
  };
};

export default Editor;