import { EditorState, SelectionState } from 'draft-js';
import insertCustomBlock from 'src/shared/utils/draftjs/insertCustomBlock';
import { ModifierStateCallbacks } from "src/Editor/types";

export const insertBlock =
  ({ setEditorState }: ModifierStateCallbacks, selectionSnapshot: SelectionState, { type, data = {} }: any) =>
    setEditorState((editorState: EditorState) => {
      const tempEditorState = EditorState.acceptSelection(editorState, selectionSnapshot);
      return insertCustomBlock(tempEditorState, type, data);
    });

export default {
  insertBlock,
};
