import {
  DefaultDraftBlockRenderMap,
} from 'draft-js';

// @ts-ignore
import blockStyles from './blocks.css';

import { Map } from 'immutable';

export const blockTypes: any = {
  'page-header': {
    element: 'h1',
  },
  'quote-one': {
    element: 'blockquote',
  },
  'quote-two': {
    element: 'blockquote',
  },
  paragraph: {
    element: 'div',
  },
  unstyled: {
    element: 'div',
  },
  image: {
    element: 'div',
  },
  video: {
    element: 'div',
  },
  delimiter: {
    element: 'div',
  },
  embedded: {
    element: 'div',
  },
  link: {
    element: 'div',
  },
};

export const blockRenderMap = DefaultDraftBlockRenderMap.merge(Map(blockTypes));

// The function will return block inline styles using block level meta-data
export const blockStyleFn = (block: any) => {
  const classes = [];
  const type = block.getType();

  const blockAlignment = block.getData() && block.getData().get('text-align');

  if (blockAlignment) {
    classes.push(`itd-${blockAlignment}-aligned-block`);
  }

  if (blockStyles[type] !== undefined) {
    classes.push(blockStyles[type]);
  }

  if (type === 'unstyled') {
    classes.push(blockStyles.defaultWrapper);
  }

  if (type === 'page-header') {
    classes.push(blockStyles.defaultWrapper);
  }
  if (type === 'header-one') {
    classes.push(blockStyles.defaultWrapper);
  }
  if (type === 'header-two') {
    classes.push(blockStyles.defaultWrapper);
  }

  if (type === 'quote-one') {
    classes.push(blockStyles.defaultWrapper);
  }
  if (type === 'quote-two') {
    classes.push(blockStyles.defaultWrapper);
  }

  if (type === 'ordered-list-item' || type === 'unordered-list-item') {
    // classes.push(blockStyles.defaultWrapper);
  }

  return classes.join(' ');
};

export const blockRendererFn = (contentBlock: any) => {
  const type = contentBlock.getType();

  return blockTypes[type] || undefined;
};
