import { Modifier, Entity, EditorState, convertToRaw } from 'draft-js';

export const checkSingleLinkEntry = (editorState: any) => {
  const selectionState = editorState.getSelection();

  // if (selectionState.isCollapsed()) {
  const anchorKey = selectionState.getAnchorKey();
  const contentState = editorState.getCurrentContent();

  const block = contentState.getBlockForKey(anchorKey);
  const blockKey = block.getKey();

  let linkKey: any;
  let linkData: any;
  let isSingleEntry: any;

  const blockText = block.get('text');

  block.findEntityRanges(
    (value: any) => {
      const entityKey = value.getEntity();
      const entity = entityKey ? Entity.get(entityKey) : null;
      const isLinkEntity = entity ? entity.getType() : null;

      if (isLinkEntity && !isSingleEntry) {
        linkKey = entityKey;
        linkData = entity && entity.getData();
        isSingleEntry = entityKey;

        return true;
      }

      return false;
    },
    (start: any, end: any) => {
      isSingleEntry = blockText.slice(start, end) === blockText.trim();
    },
  );

  return {
    linkKey,
    linkData,
    blockKey,
    isSingleEntry,
  };
};

export default {
  checkSingleLinkEntry,
};
