import React, { useCallback } from 'react';
import cn from 'classnames';

import { insertCharacter } from 'src/Editor/modifiers';

import emoji from './emojiList';
import styles from './editorEmoji.module.css';

const EditorEmojiPanel = ({ applyModifier, setContext }: any) => {
  // TODO: Change to memo
  const insertEmojiHandler = useCallback((character) => () => {
    applyModifier(insertCharacter, character);
  }, [applyModifier]);
  const cancelHandler = useCallback(() => {
    setContext(null);
  }, [setContext]);
  return (
    <div
      className={styles.wrapper}
      onMouseDown={(e) => {
        e.preventDefault();
        e.stopPropagation();
      }}
    >
      <div className={styles.leftButtons}>
        <button
          onClick={cancelHandler}
          className={cn(styles.button, styles.active)}
        >
          Cancel
        </button>

        <div className={styles.emojiContainer}>
          {
            emoji
              .map(character => (
                <button
                  key={character}
                  className={styles.emojiButton}
                  onClick={insertEmojiHandler(character)}
                >
                  {character}
                </button>
              ))
          }
        </div>
      </div>
    </div>
  );
};

export default EditorEmojiPanel;
