import React from 'react';
import cn from 'classnames';
// @ts-ignore
import styles from 'src/Editor/plugins/embedded/components/style.module.css';

export default ({ decorator }: any) => {
  const EmbedComponent = ({ blockProps }: any) => {
    const { url, isFocused } = blockProps;

    const activeClass = isFocused ? 'active' : '';

    return (
      <div className={styles.container}>
        <div className={styles.inner}>
          <div className={styles.embeddedContainer}>
            <iframe
              className={cn(styles.embedded, activeClass)}
              src={url}
              frameBorder="0"
              allowFullScreen
            />
          </div>
        </div>
      </div>
    );
  };

  return decorator ? decorator(EmbedComponent) : EmbedComponent;
};
