import React, { useCallback } from 'react';
import cn from 'classnames';

import { applyInlineStyle } from '../../modifiers/editorInlineStyle';
import { applyBlockStyle } from '../../modifiers/editorBlockStyle';
import { applyBlockSeqStyles } from '../../modifiers/editorBlockSeqStyles';

import styles from './editorText.module.css';
import { getSelectedBlock, getInlineStylesForRange } from "src/shared/utils/draftjs";
import { EditorContextType } from "src/Editor/types";

// ['bold', 'italic', 'underline', 'strikethrough', 'uppercase', 'monospace', 'superscript', 'subscript']

const preventDefault = (e: React.MouseEvent) => {
  e.preventDefault();
  e.stopPropagation();
};

const EditorTextPanel = ({ editorState, applyModifier, setContext }: any) => {
  const block = getSelectedBlock(editorState);
  const blockType = block && block.getType();
  const inlineStyle = getInlineStylesForRange(editorState);

  const isHeader = blockType === 'header-one' || blockType === 'header-two';
  const isBoldStyle = !isHeader && inlineStyle.BOLD === 'full';
  const isItalicStyle = !isHeader && inlineStyle.ITALIC === 'full';

  const linkStyles = styles.button;
  const boldStyles = cn(styles.button, { [styles.active]: isBoldStyle, [styles.disabled]: isHeader });
  const italicStyles = cn(styles.button, { [styles.active]: isItalicStyle, [styles.disabled]: isHeader });
  const quoteOneStyles = cn(styles.button, { [styles.active]: blockType === 'quote-one' });
  const quoteTwoStyles = cn(styles.button, { [styles.active]: blockType === 'quote-two' });
  const headerOneStyles = cn(styles.button, styles.big, { [styles.active]: blockType === 'header-one' });
  const headerTwoStyles = cn(styles.button, styles.small, { [styles.active]: blockType === 'header-two' });

  // TODO: Разбить на методы вместо общего каррирования
  const handleModifier = useCallback((...args) => (e: React.MouseEvent) => {
    preventDefault(e);
    applyModifier(...args);
  }, [applyModifier]);
  const switchToLinkPanel = useCallback((e: React.MouseEvent) => {
    preventDefault(e);
    setContext(EditorContextType.LINK, { forceFocus: true })
  }, [setContext]);

  return (
    <div
      className={styles.wrapper}
      onMouseDown={preventDefault}
    >
      <div className={styles.leftButtons}>
        <button
          className={styles.collapsedButtons}
          onClick={handleModifier(applyBlockSeqStyles, {
            sequence: ['header-one', 'header-two'],
            fallback: 'unstyled',
          })}
        >
          <span className={headerOneStyles}>T</span>

          <span className={headerTwoStyles}>t</span>
        </button>

        <button
          className={styles.collapsedButtons}
          onClick={handleModifier(applyBlockSeqStyles, {
            sequence: ['quote-one', 'quote-two'],
            fallback: 'unstyled',
          })}
        >
          <span className={quoteOneStyles}>Q</span>

          <span className={quoteTwoStyles}>q</span>
        </button>

        <button
          className={boldStyles}
          onClick={handleModifier(applyInlineStyle, { style: 'BOLD' })}
        >
          Bold
        </button>

        <button
          className={italicStyles}
          onClick={handleModifier(applyInlineStyle, { style: 'ITALIC' })}
        >
          Italic
        </button>

        <button
          className={linkStyles}
          onClick={switchToLinkPanel}
        >
          Link
        </button>
      </div>
    </div>
  );
};

export default EditorTextPanel;
