import {
  Entity,
  EditorState,
  ContentBlock,
  ContentState,
  RichUtils,
  Modifier,
  SelectionState,
  DefaultDraftBlockRenderMap,
} from 'draft-js';
import { List, Map } from 'immutable';

/**
 * Function returns collection of currently selected blocks.
 */
export function getSelectedBlocksMap(editorState: EditorState) {
  const selectionState = editorState.getSelection();
  const contentState = editorState.getCurrentContent();
  const startKey = selectionState.getStartKey();
  const endKey = selectionState.getEndKey();
  const blockMap = contentState.getBlockMap();
  return blockMap
    .toSeq()
    .skipUntil((_, k) => k === startKey)
    .takeUntil((_, k) => k === endKey)
    .concat([[endKey, blockMap.get(endKey)]]);
}

/**
 * Function returns collection of currently selected blocks.
 */
export function getSelectedBlocksList(editorState: EditorState) {
  return getSelectedBlocksMap(editorState).toList();
}

/**
 * Function returns the first selected block.
 */
export function getSelectedBlock(editorState: EditorState) {
  if (editorState) {
    return getSelectedBlocksList(editorState).get(0);
  }
  return undefined;
}

/**
 * Function returns the block just before the selected block.
 */
export function getBlockBeforeSelectedBlock(editorState: EditorState) {
  if (editorState) {
    const selectedBlock = getSelectedBlock(editorState);
    const contentState = editorState.getCurrentContent();
    const blockList = contentState
      .getBlockMap()
      .toSeq()
      .toList();
    let previousIndex = 0;
    blockList.forEach((block, index) => {
      if(!index || !block || !selectedBlock) return;
      if (block.getKey() === selectedBlock.getKey()) {
        previousIndex = index - 1;
      }
    });
    if (previousIndex > -1) {
      return blockList.get(previousIndex);
    }
  }
  return undefined;
}

/**
 * Function returns list of all blocks in the editor.
 */
export function getAllBlocks(editorState: EditorState) {
  if (editorState) {
    return editorState
      .getCurrentContent()
      .getBlockMap()
      .toList();
  }

  return List();
}

/**
 * If all currently selected blocks are of same type the function will return their type,
 * Else it will return empty string.
 */
export function getSelectedBlocksType(editorState: EditorState) {
  const blocks: List<ContentBlock> = getSelectedBlocksList(editorState);
  const firstBlockType = blocks.get(0).getType();
  const hasMultipleBlockTypes = blocks.some(
    (block: ContentBlock | undefined) => block ? block.getType() !== firstBlockType : false
  );
  if (!hasMultipleBlockTypes) {
    return firstBlockType;
  }
  return undefined;
}

/**
 * Function will change block style to unstyled for selected blocks.
 * RichUtils.tryToRemoveBlockStyle does not workd for blocks of length greater than 1.
 */
export function removeSelectedBlocksStyle(editorState: EditorState) {
  const newContentState = RichUtils.tryToRemoveBlockStyle(editorState);
  if (newContentState) {
    return EditorState.push(editorState, newContentState, 'change-block-type');
  }
  return editorState;
}

/**
 * Function will return currently selected text in the editor.
 */
export function getSelectionText(editorState: EditorState) {
  let selectedText = '';
  const currentSelection = editorState.getSelection();
  let start = currentSelection.getAnchorOffset();
  let end = currentSelection.getFocusOffset();
  const selectedBlocks = getSelectedBlocksList(editorState);
  if (selectedBlocks.size > 0) {
    if (currentSelection.getIsBackward()) {
      const temp = start;
      start = end;
      end = temp;
    }
    for (let i = 0; i < selectedBlocks.size; i += 1) {
      const blockStart = i === 0 ? start : 0;
      const blockEnd =
        i === selectedBlocks.size - 1
          ? end
          : selectedBlocks.get(i).getText().length;
      selectedText += selectedBlocks
        .get(i)
        .getText()
        .slice(blockStart, blockEnd);
    }
  }
  return selectedText;
}

/**
 * Function will handle followind keyPress scenario:
 * case Shift+Enter, select not collapsed ->
 *   selected text will be removed and line break will be inserted.
 */
export function addLineBreakRemovingSelection(editorState: EditorState) {
  const content = editorState.getCurrentContent();
  const selection = editorState.getSelection();
  let newContent = Modifier.removeRange(content, selection, 'forward');
  const fragment = newContent.getSelectionAfter();
  const block = newContent.getBlockForKey(fragment.getStartKey());
  newContent = Modifier.insertText(
    newContent,
    fragment,
    '\n',
    block.getInlineStyleAt(fragment.getStartOffset()),
    undefined
  );
  return EditorState.push(editorState, newContent, 'insert-fragment');
}

/**
 * Function will inset a new unstyled block.
 */
export function insertNewUnstyledBlock(editorState: EditorState) {
  const newContentState = Modifier.splitBlock(
    editorState.getCurrentContent(),
    editorState.getSelection()
  );
  const newEditorState = EditorState.push(
    editorState,
    newContentState,
    'split-block'
  );
  return removeSelectedBlocksStyle(newEditorState);
}

/**
 * Function will clear all content from the editor.
 */
export function clearEditorContent(editorState: EditorState) {
  const blocks = editorState
    .getCurrentContent()
    .getBlockMap()
    .toList();
  const updatedSelection = editorState.getSelection().merge({
    anchorKey: blocks.first().get('key'),
    anchorOffset: 0,
    focusKey: blocks.last().get('key'),
    focusOffset: blocks.last().getLength(),
  }) as SelectionState;
  const newContentState = Modifier.removeRange(
    editorState.getCurrentContent(),
    updatedSelection,
    'forward'
  );
  return EditorState.push(editorState, newContentState, 'remove-range');
}

/**
 * Function to set text of block.
 */
export function setBlockContent(editorState: EditorState, contentData: { text?: string, type: string, data: any }) {
  const selectionState = editorState.getSelection();
  const contentState = editorState.getCurrentContent();
  const blockMap = contentState.getBlockMap();
  const blockKey = selectionState.getStartKey();
  const newContentState = contentState.merge({
    blockMap: blockMap.map((block, key) => {
      return block && key === blockKey ? block.merge(contentData) : block})
  }) as ContentState;
  return EditorState.push(editorState, newContentState, 'insert-characters');
}

/**
 * Function to set text of block.
 */
export function setBlockText(editorState: EditorState, newText: string) {
  const selectionState = editorState.getSelection();
  const contentState = editorState.getCurrentContent();
  const blockMap = contentState.getBlockMap();
  const blockKey = selectionState.getStartKey();
  const newContentState = contentState.merge({
    blockMap: blockMap.map((block, key) => block && key === blockKey ? block.set('text', newText) : block)
  }) as ContentState;
  return EditorState.push(editorState, newContentState, 'insert-characters');
}

/**
 * Function will add block level meta-data.
 */
export function setBlockData(editorState: EditorState, data: any) {
  const newContentState = Modifier.setBlockData(
    editorState.getCurrentContent(),
    editorState.getSelection(),
    data
  );
  return EditorState.push(editorState, newContentState, 'change-block-data');
}

/**
 * Function will change block level type.
 */
export function setBlockType(editorState: EditorState, type: string) {
  const newContentState = Modifier.setBlockType(
    editorState.getCurrentContent(),
    editorState.getSelection(),
    type
  );
  return EditorState.push(editorState, newContentState, 'change-block-type');
}

/**
 * Function will return currently selected blocks meta data.
 */
export function getSelectedBlocksMetadata(editorState: EditorState) {
  let metaData = Map({});
  const selectedBlocks = getSelectedBlocksList(editorState);
  if (selectedBlocks && selectedBlocks.size > 0) {
    for (let i = 0; i < selectedBlocks.size; i += 1) {
      const data = selectedBlocks.get(i).getData();
      if (!data || data.size === 0) {
        metaData = metaData.clear();
        break;
      }
      if (i === 0) {
        metaData = data;
      } else {
        metaData.forEach((value: any, key: any) => {
          // eslint-disable-line no-loop-func
          if (!data.get(key) || data.get(key) !== value) {
            metaData = metaData.delete(key);
          }
        });
        if (metaData.size === 0) {
          metaData = metaData.clear();
          break;
        }
      }
    }
  }
  return metaData;
}

export const removeBlock = (editorState: EditorState, blockKey: string) => {

  let content = editorState.getCurrentContent();

  const afterKey = content.getKeyAfter(blockKey);
  const afterBlock = content.getBlockForKey(afterKey);

  const newSelection = new SelectionState({
    anchorKey: blockKey,
    anchorOffset: 0,
    focusKey: blockKey,
    focusOffset: 0
  });

  // Only if the following block the last with no text then the whole block
  // should be removed. Otherwise the block should be reduced to an unstyled block
  // without any characters.
  const isLastBlock = (
    afterBlock && afterBlock.getType() === 'unstyled' &&
    afterBlock.getLength() === 0 && afterBlock === content.getBlockMap().last()
  );

  const targetRange = new SelectionState({
    anchorKey: blockKey,
    anchorOffset: 0,
    focusKey: isLastBlock ? afterKey : blockKey,
    focusOffset: isLastBlock ? 0 : 1,
  });

  // change the blocktype and remove the characterList entry with the block
  content = Modifier.setBlockType(content, targetRange, 'unstyled');
  content = Modifier.removeRange(content, targetRange, 'backward');

  // force to new selection
  const newState = EditorState.push(editorState, content, 'remove-range');
  return EditorState.forceSelection(newState, newSelection);
};

export const setEntityData = (contentBlock: any, { getEditorState, setEditorState }: any) => (data: any) => {
  const entityKey = contentBlock.getEntityAt(0);
  if (entityKey) {
    const editorState = getEditorState();
    Entity.mergeData(entityKey, data);
    setEditorState(EditorState.forceSelection(editorState, editorState.getSelection()));
  }
};

const newBlockRenderMap = Map({
  code: {
    element: 'pre',
  },
});

export const blockRenderMap = DefaultDraftBlockRenderMap.merge(
  newBlockRenderMap
);
