import { EditorState, Modifier, RichUtils, Entity, SelectionState } from 'draft-js';
import {
  getSelectionText,
  getEntityRange,
  getSelectionEntity,
} from 'src/shared/utils/draftjs';
import { ModifierStateCallbacks } from "src/Editor/types";

function addLinkEntity(editorState: EditorState, linkTarget: any) {
  let selection = editorState.getSelection();

  const linkTitle = getSelectionText(editorState);
  const currentEntity = getSelectionEntity(editorState);

  if (currentEntity) {
    const entityRange: any = getEntityRange(editorState, currentEntity);
    selection = selection.merge({
      anchorOffset: entityRange.start,
      focusOffset: entityRange.end,
    }) as SelectionState;
  }

  const entityKey = Entity.create('LINK', 'MUTABLE', { url: linkTarget });

  let contentState = Modifier.replaceText(
    editorState.getCurrentContent(),
    selection,
    `${linkTitle}`,
    editorState.getCurrentInlineStyle(),
    entityKey,
  );
  let newEditorState = EditorState.push(editorState, contentState, 'insert-characters');

  // insert a blank space after link
  selection = newEditorState.getSelection().merge({
    anchorOffset: selection.get('anchorOffset') + linkTitle.length,
    focusOffset: selection.get('anchorOffset') + linkTitle.length,
  }) as SelectionState;

  newEditorState = EditorState.acceptSelection(newEditorState, selection);

  contentState = Modifier.insertText(
    newEditorState.getCurrentContent(),
    selection,
    ' ',
    newEditorState.getCurrentInlineStyle(),
    undefined,
  );

  return EditorState.push(newEditorState, contentState, 'insert-characters');
}

export const removeLink = ({ setEditorState }: ModifierStateCallbacks, selectionSnapshot: SelectionState) =>
  setEditorState((editorState: EditorState) => {
    const tempEditorState = EditorState.acceptSelection(editorState, selectionSnapshot);

    const currentEntity = getSelectionEntity(tempEditorState);

    if (!currentEntity) return editorState;

    const entityRange: any = getEntityRange(tempEditorState, currentEntity);

    const newSelection: any = selectionSnapshot.merge({
      anchorOffset: entityRange.start,
      focusOffset: entityRange.end,
    });

    return RichUtils.toggleLink(tempEditorState, newSelection, null);
  });

export const addLink = (
  { setEditorState }: ModifierStateCallbacks,
  selectionSnapshot: SelectionState,
  { url }: any,
) =>
  setEditorState((editorState: EditorState) => {
    const tempEditorState = EditorState.acceptSelection(editorState, selectionSnapshot);

    return addLinkEntity(tempEditorState, url);
  });
