import createInnerFocusPlugin from './innerFocus';
// import createOuterFocusPlugin from './outerFocus';
import createImagePlugin from './image';
import createVideoPlugin from './video';
import createLinkPlugin from './link/index';
import createAutoListPlugin from './autoList';
import createBlockBreakoutPlugin from './blockBreakout';
import createDelimiterPlugin from './delimiter';
import createEmbeddedPlugin from './embedded';
import createEntityPropsPlugin from './entityProps';
// import createSplitHeadersPlugin from './splitHeaders';

import { ApplyModifier } from "src/Editor/index";

export default (applyModifier: ApplyModifier) => {
  const innerFocusPlugin = createInnerFocusPlugin({});

  const decoratedWithFocus = {
    decorator: innerFocusPlugin.decorator,
  };

  // const outerFocusPlugin = createOuterFocusPlugin({});
  const autoListPlugin = createAutoListPlugin();
  const entityPropsPlugin = createEntityPropsPlugin({});
  const blockBreakoutPlugin = createBlockBreakoutPlugin({});
  const delimiterPlugin = createDelimiterPlugin({});

  const linkPlugin = createLinkPlugin({ applyModifier });
  const imagePlugin = createImagePlugin(decoratedWithFocus);
  const videoPlugin = createVideoPlugin(decoratedWithFocus);
  const embeddedPlugin = createEmbeddedPlugin(decoratedWithFocus);

  return [
    autoListPlugin,
    innerFocusPlugin,
    // outerFocusPlugin,
    entityPropsPlugin,
    delimiterPlugin,
    embeddedPlugin,
    // splitHeaderPlugin,
    linkPlugin,
    imagePlugin,
    videoPlugin,
    blockBreakoutPlugin,
  ];
};
