import React, { useCallback } from 'react';

import { applyBlockStyle } from 'src/Editor/modifiers/editorBlockStyle';

export const CONTEXT_TYPE = 'editor-quote';

export default ({ id, editorState, applyModifier, changeContext }: any) => {
  const quoteOne = useCallback(() =>
    applyModifier(applyBlockStyle, { style: 'quote-one' }), [applyModifier]);
  const quoteTwo = useCallback(() =>
    applyModifier(applyBlockStyle, { style: 'quote-two' }), [applyModifier]);
  const quoteThree = useCallback(() =>
    applyModifier(applyBlockStyle, { style: 'quote-three' }), [applyModifier]);

  return (
    <div
      onMouseDown={(e) => {
        e.preventDefault();
        e.stopPropagation();
      }}
    >
      <button
        onClick={quoteOne}
      >
        Quote 1
      </button>

      <button
        onClick={quoteTwo}
      >
        Quote 2
      </button>

      <button
        onClick={quoteThree}
      >
        Quote 3
      </button>
    </div>
  );
};
