import { CharacterMetadata, EditorState } from 'draft-js';
type StylesState = Record<string, 'full' | 'partial'>;
type StylesQuantity = Record<string, number>;

export const getInlineStylesForRange = (editorState: EditorState) => {
  const selection = editorState.getSelection();
  const anchorKey = selection.getAnchorKey();
  const startOffset = selection.getStartOffset();
  const endOffset = selection.getEndOffset();
  const charLength = endOffset - startOffset;
  const contentState = editorState.getCurrentContent();
  const blockMap = contentState.getBlockMap();
  const block = blockMap.get(anchorKey);
  const charList = block.getCharacterList();

  // TODO: Не учитывается корректная разбивка по блокам, учитывается только блок старовой позиции выделения
  // Необходимо ввести getSelectedBlocksMap

  // Считаем количество символов в каждом отдельном вхождении стилей
  const styleQuantity =
    charList.reduce<StylesQuantity>((stylesMemo = {}, char?: CharacterMetadata, i?: number) => {
      if (char && (i !== undefined && i >= startOffset && i < endOffset)) {
        char.getStyle().forEach((style: any) => {
          stylesMemo[style] = stylesMemo[style] + 1 || 1;
        });
      }

      return stylesMemo;
    }, Object.create(null));

  // Возвращаем все найденные стили с пометкой, занимает он весь диапазон выделенного текста или нет
  return Object.keys(styleQuantity)
    .reduce<StylesState>((stylesMemo = {}, style: string) => {
      stylesMemo[style] = styleQuantity[style] === charLength ? 'full' : 'partial';

      return stylesMemo;
    }, Object.create(null));
};
