import creator from './components/image';
import { storedFiles } from 'src/tempStore/files';

const imagePlugin = (config = {}) => {
  const component = creator(config);

  return {
    blockRendererFn: (contentBlock: any, { getProps }: any) => {
      if (contentBlock.getType() !== 'image') {
        return;
      }

      const { fileId, url = '', style }: any = contentBlock.data ? contentBlock.data.toObject() : {};

      const actualUrl = storedFiles[fileId] ? storedFiles[fileId].url : url;

      return {
        isAtomic: true,
        editable: false,
        component,
        props: {
          url: actualUrl,
          style,
          isAtomic: true,
        },
      };
    },
  };
};

export default imagePlugin;
