import { EditorState, Modifier, SelectionState } from 'draft-js';
import { ModifierStateCallbacks } from "src/Editor/types";

export const insertCharacter =
  ({ setEditorState }: ModifierStateCallbacks, selectionSnapshot: SelectionState, character: any) =>
    setEditorState((editorState: EditorState) => {
      const tempEditorState = EditorState.acceptSelection(editorState, selectionSnapshot);
      const newContentState = Modifier.insertText(
        editorState.getCurrentContent(),
        selectionSnapshot,
        `${character} `,
        editorState.getCurrentInlineStyle(),
      );
      const newEditorState = EditorState.push(tempEditorState, newContentState, 'insert-characters');

      return EditorState.forceSelection(
        newEditorState,
        newContentState.getSelectionAfter(),
      );
    });

export default {
  insertCharacter,
};
