import { EditorState, SelectionState, genKey } from 'draft-js';
import insertCustomBlock from 'src/shared/utils/draftjs/insertCustomBlock';
import { EditorContextType, ModifierStateCallbacks } from "src/Editor/types";
import { storedFiles, StoredFiles, ImageData } from 'src/tempStore/files';
import { getImageInfo } from 'src/api/images';

export const insertImages =
  ({ setEditorState }: ModifierStateCallbacks, selectionSnapshot: SelectionState, images: any = []) =>
    setEditorState((editorState: EditorState) => {
      const tempEditorState = EditorState.acceptSelection(editorState, selectionSnapshot);

      return images.reduce((state: any, { key }: any) =>
        insertCustomBlock(state, EditorContextType.IMAGE, { fileId: key }, key), tempEditorState);
    });

export const uploadImages =
  ({ setEditorState }: ModifierStateCallbacks, selectionSnapshot: SelectionState, fileList: FileList) => {
    let dataPromises: Promise<ImageData>[] = [];

    for (let i = 0; i < fileList.length; i += 1) {
      const blockKey = genKey();
      const dataPromise = getImageInfo(fileList.item(i) as File, blockKey);

      dataPromises.push(dataPromise);
    }

    Promise.all(dataPromises)
      .then((imagesData: ImageData[]) => {
        const imageDataList = imagesData
          .reduce((memo, { key, data }) =>
            Object.assign(memo, { [key]: { url: data } }), {} as StoredFiles);

        Object.assign(storedFiles, imageDataList);

        setEditorState((editorState: EditorState) => {
          const tempEditorState = EditorState.acceptSelection(editorState, selectionSnapshot);
          return imagesData.reduce((state: any, { key }: any) =>
            insertCustomBlock(state, EditorContextType.IMAGE, { fileId: key }, key), tempEditorState);
        });
      });
  };


export default {
  insertImages,
};
