import creator from './components/video';
import { EditorContextType } from "src/Editor/types";

const videoPlugin = (config: any = {}) => {
  const component = creator(config);

  return {
    blockRendererFn: (contentBlock: any, { getProps }: any) => {
      if (contentBlock.getType() !== EditorContextType.VIDEO) {
        return;
      }

      const { url, style }: any = contentBlock.data ? contentBlock.data.toObject() : {};

      return {
        component,
        editable: false,
        props: {
          url,
          style,
        },
      };
    },
  };
};

export default videoPlugin;

export const videoTheme = {};
export const videoCreator = creator;
