import React, { Component } from 'react';
import cn from 'classnames';

import {
  getSelectedBlocksType,
  getSelectedBlocksMetadata,
} from 'src/shared/utils/draftjs';

import { insertBlock } from 'src/Editor/modifiers';

import styles from './editorEmbed.module.css';
import { EditorContextType } from "src/Editor/types";

export const CONTEXT_TYPE = 'editor-embed';

const DEFAULT_URL = 'data:text/html;charset=utf-8,%3Cbody%3E%3Cscript%20src%3D%22https%3A%2F%2Fgist.github.com%2FStGeass%2Fec2e53ac515c3eda1e8f4e22d21452cd.js%22%3E%3C%2Fscript%3E%3C%2Fbody%3E';

// TODO: Это что за хуйня? Этот модификатор не реализован???
const removeEmbed: any = () => {};

class EditorEmbedPanel extends Component<any, any> {
  urlInput: any;

  constructor(props: any) {
    super(props);

    const { editorState } = props;

    const blocksType = getSelectedBlocksType(editorState);
    const isCollapsed = editorState.getSelection().isCollapsed();
    const isAlreadyEmbed = isCollapsed && blocksType === 'embedded';
    const blockData = isAlreadyEmbed ? getSelectedBlocksMetadata(editorState) : null;

    const url = blockData ? blockData.get('url') : DEFAULT_URL;

    this.state = {
      url,
      isAlreadyEmbed,
    };
  }

  componentDidMount() {
    const { forceFocus } = this.props;

    if (forceFocus) {
      setTimeout(() => this.urlInput.focus(), 50);
    }
  }

  componentWillReceiveProps(nextProps: any) {
    const { editorState } = nextProps;

    const blocksType = getSelectedBlocksType(editorState);
    const isCollapsed = editorState.getSelection().isCollapsed();
    const isAlreadyEmbed = isCollapsed && blocksType === 'embedded';
    const blockData = isAlreadyEmbed ? getSelectedBlocksMetadata(editorState) : null;

    const url = blockData ? blockData.get('url') : DEFAULT_URL;

    this.setState({
      url,
      isAlreadyEmbed,
    });
  }

  insertEmbed = (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();

    const { applyModifier } = this.props;
    const { url } = this.state;

    applyModifier(insertBlock, { type: EditorContextType.EMBED, data: { url } });
  };

  onUrlInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    this.setState({ url: e.target.value });
  };

  cancel = () => {
    const { setContext } = this.props;
    setContext(null)
  };

  render() {
    const { url, isAlreadyEmbed } = this.state;
    const { applyModifier } = this.props;

    return (
      <div className={styles.wrapper}>
        <div className={styles.leftButtons}>
          <input
            className={styles.input}
            ref={(ref) => {
              this.urlInput = ref;
            }}
            type="text"
            value={url}
            onChange={this.onUrlInputChange}
            placeholder="http://"
          />
        </div>

        <div className={styles.centerButtons}>
          <button
            className={cn(styles.button, styles.active)}
            onClick={this.insertEmbed}
          >
            OK
          </button>


          {
            isAlreadyEmbed ? (
              <button
                className={styles.button}
                onClick={applyModifier(removeEmbed)}
              >
                Remove
              </button>
            ) : (
              <button
                className={styles.button}
                onClick={this.cancel}
              >
                Cancel
              </button>
            )
          }
        </div>
      </div>
    );
  }
}

export default EditorEmbedPanel;
