import React, { useCallback } from 'react';

import { insertBlock } from 'src/Editor/modifiers/editorInsertBlock';
import { uploadImages } from 'src/Editor/modifiers/editorInsertImages';

import styles from './editorBlock.module.css';
import { EditorContextType } from "src/Editor/types";

// TODO: API
const uploadImage: any = () => {
};
const getImageInfo: any = () => {
};

const preventDefault = (e: React.MouseEvent) => {
  e.preventDefault();
  e.stopPropagation();
};

const EditorBlockPanel = ({ setContext, applyModifier }: any) => {
  const setContextToBlock = useCallback(() =>
    setContext(EditorContextType.BLOCK), [setContext]);
  const setContextToVideo = useCallback(() =>
    setContext(EditorContextType.VIDEO), [setContext]);
  const setContextToEmbed = useCallback(() =>
    setContext(EditorContextType.EMBED), [setContext]);
  const setContextToEmoji = useCallback(() =>
    setContext(EditorContextType.EMOJI), [setContext]);

  const addImages = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    applyModifier(uploadImages, e.target.files);
  }, [applyModifier]);

  const insertDelimiter = useCallback(() =>
    applyModifier(insertBlock, { type: 'delimiter' }), [applyModifier]);

  return (
    <div
      className={styles.wrapper}
      onMouseDown={preventDefault}
    >
      <div className={styles.leftButtons}>
        <label
          className={styles.button}
          onMouseUp={setContextToBlock}
          htmlFor="editor-panel-image-file-input"
        >
          Add image

          <input
            id="editor-panel-image-file-input"
            name="editor-panel-image-file-input"
            type="file"
            className={styles.fileInput}
            onChange={addImages}
            accept="image/*"
            multiple
          />
        </label>

        <button
          className={styles.button}
          onClick={setContextToVideo}
        >
          Video
        </button>

        <button
          className={styles.button}
          onClick={setContextToEmbed}
        >
          Embed
        </button>

        <button
          className={styles.button}
          onClick={insertDelimiter}
        >
          Delimiter
        </button>

        <button
          className={styles.button}
          onClick={setContextToEmoji}
        >
          Emoji
        </button>
      </div>
    </div>
  )
};

export default EditorBlockPanel;
