import { EditorState, SelectionState } from 'draft-js';
import { ModifierStateCallbacks } from "src/Editor/types";
import { setBlockContent } from "src/shared/utils/draftjs/block";

enum ImageTypes {
  PNG = 'png',
  JPEG = 'jpeg',
}

type UrlResponse = {
  img_type?: 'png' | 'jpeg',
  ogp?: {
    url: string,
    title: string,
    description: string,
    images: string[],
  }
};

type ImageBlockData = {
  url: string,
  style: string,
};

type LinkBlockData = {
  url: string,
  image: string | null,
  title: string,
  description: string,
};

export const editorBlockLink = (
  { setEditorState }: ModifierStateCallbacks,
  selectionSnapshot: SelectionState,
  { url, blockKey }: any,
) => {
  Promise
    .resolve(url)
    .then((url) => {
      if (/\.png/gi.test(url)) {
        return { img_type: ImageTypes.PNG };
      }
      if (/\.jpeg|\.jpg/gi.test(url)) {
        return { img_type: ImageTypes.JPEG };
      }
      return {
        ogp: {
          url,
          title: 'Mock title',
          description: 'Mock description',
          images: [
            'https://ci.phncdn.com/www-static/images/pornhub_logo_straight.png',
          ],
        }
      }
    })
    .then(({ img_type: imgType, ogp }: UrlResponse) => {
      setEditorState((editorState: EditorState) => {
        let type: string;
        let data: ImageBlockData | LinkBlockData;
        let newEditorState = EditorState.acceptSelection(editorState, selectionSnapshot);

        if (imgType === ImageTypes.PNG || imgType === ImageTypes.JPEG) {
          type = 'image';
          data = { url, style: 'by-text' };
        } else if (ogp) {
          const image = ogp.images && ogp.images.length ? ogp.images[0] : null;
          type = 'link';
          data = {
            url: ogp.url,
            image,
            title: ogp.title,
            description: ogp.description,
          };
        } else {
          return editorState;
        }

        newEditorState = setBlockContent(newEditorState, { type, data, text: '' });

        return newEditorState;
      });
    });
};
