import React, { useState, useCallback, useEffect } from "react";
import {
  EditorState,
  SelectionState,
  Modifier,
  getVisibleSelectionRect,
  CompositeDecorator,
  convertToRaw,
  AtomicBlockUtils,
  ContentBlock
} from 'draft-js';
import PluginsEditor from 'draft-js-plugins-editor';

import styles from './styles.module.css';
import BotPanel from 'src/Editor/components/botPanel';
import Editor, { useEditor } from 'src/Editor';
import getPlugins from 'src/Editor/plugins';
import { EditorContextType, EditorModifierWrapper, ModifierStateCallbacks } from 'src/Editor/types';

type EditorContext = {
  type: EditorContextType,
  meta?: Object,
} | null;

// TODO: Вынести к остальным модификаторам?
const updateEditorStateImmediately = (
  { setEditorState }: ModifierStateCallbacks,
  selectionState: SelectionState,
  newEditorState: EditorState,
) => {
  setEditorState(() => newEditorState, true);
};

const MOBILE_EVENT_NAME = 'message';

const EDITOR_DATA_INPUT = 'EDITOR_DATA_INPUT';
const EDITOR_DATA_OUTPUT = 'EDITOR_DATA_OUTPUT';

const eventPattern = new RegExp(EDITOR_DATA_INPUT, 'gi');

export default (document: any) => {
  const editor = React.useRef<PluginsEditor>(null);

  const [editorRawState, setEditorRawState] = React.useState<any>(
    document.editorRawState || convertToRaw(EditorState.createEmpty().getCurrentContent())
  );
  const { editorState, applyModifier } = useEditor(editorRawState);

  const [context, setContext] = useState<EditorContext>(null);
  const onContextChange = useCallback((type: EditorContextType | null, meta?: any) => {
    const newContext = type ? { type, meta } : null;
    setContext(newContext);
  }, []);
  const onStateChange = useCallback((newEditorState: EditorState) => {
    applyModifier(updateEditorStateImmediately, newEditorState);
  }, [applyModifier]);
  const clearContext = () => {

  };

  useEffect(() => {
    if (!('ReactNativeWebView' in window)) return;

    // @ts-ignore
    window.ReactNativeWebView.postMessage(JSON.stringify({
      type: EDITOR_DATA_OUTPUT,
      payload: convertToRaw(editorState.getCurrentContent()),
    }));
  }, [editorState]);

  useEffect(() => {
    const eventListener = ({ data }: { data: { type: string, payload: string } }) => {
      if (!data || typeof data !== 'string' || !eventPattern.test(data)) return;

      try {
        const { type, payload } = JSON.parse(data);

        if (type === EDITOR_DATA_INPUT) {
          setEditorRawState(payload);
        }
      } catch (e) {
        console.error(e);
      }
    };

    window.addEventListener(MOBILE_EVENT_NAME, eventListener);

    return () => {
      window.removeEventListener(MOBILE_EVENT_NAME, eventListener);
    };
  }, []);

  const plugins = React.useRef(getPlugins(applyModifier));

  return (
    <div className={styles.wrapper}>
      <div className={styles.contentWrapper}>
        <Editor
          readOnly={false}
          isPresentation={false}
          files={[]}
          plugins={plugins.current}
          context={context}
          clearContext={clearContext}
          onStateChange={onStateChange}
          onContextChange={onContextChange}
          editorState={editorState}
        />
      </div>

      <BotPanel
        context={context}
        setContext={onContextChange}
        editorState={editorState}
        applyModifier={applyModifier}
      />
    </div>
  );
};
