import { EditorState, SelectionState } from 'draft-js';

export enum EditorContextType {
  TEXT = 'TEXT',
  LINK = 'LINK',
  QUOTE = 'QUOTE',
  IMAGE = 'IMAGE',
  BLOCK = 'BLOCK',
  EMBED = 'EMBED',
  EMOJI = 'EMOJI',
  VIDEO = 'VIDEO',
  HEADER = 'HEADER',
}

export type ModifierStateCallbacks = {
  getEditorState: () => EditorState,
  setEditorState: EditorModifier;
};
export type EditorModifier = (editorStateSetter: EditorStateSetter, immediately?: boolean) => void;
export type EditorStateSetter = (editorState: EditorState) => EditorState;
export type EditorModifierWrapper = (
  callbacks: ModifierStateCallbacks,
  selection: SelectionState,
  meta?: any,
) => void;
