import { EditorState, RichUtils, SelectionState } from 'draft-js';
import { ModifierStateCallbacks } from "src/Editor/types";

export const applyBlockStyle = (
  { setEditorState }: ModifierStateCallbacks,
  selectionSnapshot: SelectionState,
  { style }: any,
) =>
  setEditorState((editorState: EditorState) => {
    const tempEditorState = EditorState.acceptSelection(editorState, selectionSnapshot);

    return RichUtils.toggleBlockType(tempEditorState, style);
  });

export default {
  applyBlockStyle,
};
