import creator from './components/delimiter';

const delimiterPlugin = (config: any = {}) => {
  const component = creator(config);

  return {
    blockRendererFn: (contentBlock: any, { getProps }: any) => {
      if (contentBlock.getType() !== 'delimiter') {
        return;
      }

      return {
        isAtomic: true,
        editable: false,
        component,
        props: {
          isAtomic: true,
        },
      };
    },
  };
};

export default delimiterPlugin;
