import {
  EditorState,
  Modifier,
  RichUtils,
  SelectionState,
  getDefaultKeyBinding,
  // convertToRaw,
  DraftHandleValue,
} from 'draft-js';
// import { changeDepth } from 'src/shared/utils/draftjs';
import trackCharacters from './trackCharacters';

const UL_FULL_REGEX = /^\s*(\*|-)$/;
const UL_INTERCEPT_REGEX = /^(\*|-)$/;
const OL_FULL_REGEX = /^\s*\d+\.$/;
const OL_INTERCEPT_REGEX = /^\d+\.$/;

const commands = {
  OL: 'autolist-ordered',
  UL: 'autolist-unordered',
};

const blockTypes = {
  UL: 'unordered-list-item',
  OL: 'ordered-list-item',
  UNSTYLED: 'unstyled',
};

const MAX_DEPTH = 4;

function makeList(editorState: any) {

}

function autoListPlugin() {
  let keyCharsHistory: any = [];

  return {

    keyBindingFn(e: any, { getEditorState, setEditorState }: any) { // eslint-disable-line consistent-return
      const editorState = getEditorState();
      keyCharsHistory = trackCharacters(keyCharsHistory, e);

      const defaultCommand = getDefaultKeyBinding(e);

      if (defaultCommand === 'backspace' || defaultCommand === 'backspace-word') {

        const selectionState = editorState.getSelection();
        const anchorKey = selectionState.getAnchorKey();
        const currentContent = editorState.getCurrentContent();
        const currentBlock = currentContent.getBlockForKey(anchorKey);
        const currentBlockLength = currentBlock.getLength();

        if (!currentBlockLength) {
          const newEditorState = RichUtils.toggleBlockType(editorState, 'unstyled');

          setEditorState(newEditorState);

          return defaultCommand;
        }
      }

      // Only run our more complex checks if the last character
      // typed is a space
      if (e.keyCode === 32) {
        const selectionState = editorState.getSelection();

        if (selectionState.isCollapsed()) {
          const anchorKey = selectionState.getAnchorKey();
          const currentContent = editorState.getCurrentContent();
          const currentBlock = currentContent.getBlockForKey(anchorKey);
          const currentBlockKey = currentBlock.getKey();
          const currentBlockType = currentBlock.getType();
          const currentBlockText = currentBlock.getText();
          const currentBlockDepth = currentBlock.getDepth();
          const currentBlockLength = currentBlock.getLength();

          if (currentBlockType === blockTypes.UNSTYLED && currentBlockDepth === 0) {
            const isUL = UL_FULL_REGEX.test(currentBlockText);
            const isOL = OL_FULL_REGEX.test(currentBlockText);

            if (isUL || isOL) {
              e.preventDefault();

              const listType = isUL ? blockTypes.UL : blockTypes.OL;

              // Select the entire block
              const blockSelection = new SelectionState({
                anchorKey: currentBlockKey,
                anchorOffset: 0,
                focusKey: currentBlockKey,
                focusOffset: currentBlockLength,
              });

              let newEditorState = RichUtils.toggleBlockType(editorState, listType);

              // Replace with the text with either nothing (if we created a list)
              // or the existing content
              let newContent = newEditorState.getCurrentContent();

              newContent = Modifier.replaceText(
                newContent,
                blockSelection,
                '',
              );

              const selectionAfter = newContent.getSelectionAfter();

              // TODO: Перепроврить EditorChangeType
              newEditorState = EditorState.push(newEditorState, newContent, 'insert-fragment');
              newEditorState = EditorState.forceSelection(newEditorState, selectionAfter);

              setEditorState(newEditorState);
            }
          }
        }
      }

      // TODO: Посмотреть почему теперь необходимо всегда возвращать null или EditorCommand
      return defaultCommand;
    },

    onTab(e: any, { getEditorState, setEditorState }: any) {
      const editorState = getEditorState();
      const selectionState = editorState.getSelection();
      const startKey = selectionState.getStartKey();
      const endKey = selectionState.getEndKey();
      const anchorKey = selectionState.getAnchorKey();
      const currentContent = editorState.getCurrentContent();
      const currentBlock = currentContent.getBlockForKey(anchorKey);
      const currentBlockKey = currentBlock.getKey();
      const currentBlockType = currentBlock.getType();
      const currentBlockText = currentBlock.getText();
      const currentBlockDepth = currentBlock.getDepth();
      const currentBlockLength = currentBlock.getLength();

      if (e.shiftKey) {
        if (currentBlockType === blockTypes.UL || currentBlockType === blockTypes.OL) {
          e.preventDefault();

          const blockMap = currentContent.getBlockMap();
          let nextMaxDepth;

          const selectedBlockMap = blockMap
            .toSeq()
            .skipUntil((_: any, k: any) => k === startKey)
            .takeUntil((_: any, k: any) => k === endKey)
            .concat([[endKey, blockMap.get(endKey)]])
            .map((block: any, key: any) => {
              const blockType = block.getType();
              const blockDepth = block.getDepth();
              const nextDepth = blockDepth + 1;

              if (blockType === blockTypes.UL || blockType === blockTypes.OL) {
                // let depth;
                //
                // if (nextMaxDepth !== undefined) {
                //   depth = Math.max(0, Math.min(nextDepth, MAX_DEPTH, nextMaxDepth));
                // } else {
                //   const previousBlock = currentContent.getBlockBefore(key);
                //   const previousBlockType = previousBlock && previousBlock.getType();
                //
                //   if (
                //     previousBlockType === blockTypes.UL ||
                //     previousBlockType === blockTypes.OL
                //   ) {
                //     const previousBlockDepth = previousBlock && previousBlock.getDepth();
                //
                //     depth = Math.min(nextDepth, previousBlockDepth + 1);
                //   } else {
                //     depth = blockDepth;
                //   }
                // }
                //
                // nextMaxDepth = Math.min(depth + 1, MAX_DEPTH);
                // console.error('After:', { key, depth, nextMaxDepth, nextDepth });

                const depth = Math.max(0, blockDepth - 1);

                return block.set('depth', depth).set('text', key);
              }

              return block;
            });

          const newContentState = currentContent.merge({
            blockMap: blockMap.merge(selectedBlockMap),
            selectionBefore: selectionState,
            selectionAfter: selectionState,
          });

          // TODO: Перепроврить EditorChangeType
          const newEditorState = EditorState.push(editorState, newContentState, 'insert-fragment');

          setEditorState(newEditorState);
        }
      } else {
        if (currentBlockType === blockTypes.UNSTYLED && currentBlockDepth === 0) {
          e.preventDefault();

          const isUL = UL_FULL_REGEX.test(currentBlockText);
          const isOL = OL_FULL_REGEX.test(currentBlockText);

          if (isUL || isOL) {
            e.preventDefault();

            const listType = isUL ? blockTypes.UL : blockTypes.OL;

            // Select the entire block
            const blockSelection = new SelectionState({
              anchorKey: currentBlockKey,
              anchorOffset: 0,
              focusKey: currentBlockKey,
              focusOffset: currentBlockLength,
            });

            let newEditorState = RichUtils.toggleBlockType(editorState, listType);

            // Replace with the text with either nothing (if we created a list)
            // or the existing content
            let newContent = newEditorState.getCurrentContent();

            newContent = Modifier.replaceText(
              newContent,
              blockSelection,
              '',
            );

            const selectionAfter = newContent.getSelectionAfter();

            // TODO: Перепроврить EditorChangeType
            newEditorState = EditorState.push(newEditorState, newContent, 'insert-fragment');
            newEditorState = EditorState.forceSelection(newEditorState, selectionAfter);

            setEditorState(newEditorState);
          }
        } else {
          e.preventDefault();

          if (currentBlockType === blockTypes.UL || currentBlockType === blockTypes.OL) {
            const blockMap = currentContent.getBlockMap();
            let nextMaxDepth: any;

            const selectedBlockMap = blockMap
              .toSeq()
              .skipUntil((_: any, k: any) => k === startKey)
              .takeUntil((_: any, k: any) => k === endKey)
              .concat([[endKey, blockMap.get(endKey)]])
              .map((block: any, key: any) => {
                let depth;
                const blockType = block.getType();
                const blockDepth = block.getDepth();
                const nextDepth = blockDepth + 1;

                if (blockType === blockTypes.UL || blockType === blockTypes.OL) {
                  if (nextMaxDepth !== undefined) {
                    depth = Math.max(0, Math.min(nextDepth, MAX_DEPTH, nextMaxDepth));
                  } else {
                    const previousBlock = currentContent.getBlockBefore(key);
                    const previousBlockType = previousBlock && previousBlock.getType();

                    if (
                      previousBlockType === blockTypes.UL ||
                      previousBlockType === blockTypes.OL
                    ) {
                      const previousBlockDepth = previousBlock && previousBlock.getDepth();

                      depth = Math.min(nextDepth, previousBlockDepth + 1);
                    } else {
                      depth = blockDepth;
                    }
                  }

                  nextMaxDepth = Math.min(depth + 1, MAX_DEPTH);

                  return block.set('depth', depth);
                }

                return block;
              });

            const newContentState = currentContent.merge({
              blockMap: blockMap.merge(selectedBlockMap),
              selectionBefore: selectionState,
              selectionAfter: selectionState,
            });

            // TODO: Перепроврить EditorChangeType
            const newEditorState = EditorState.push(editorState, newContentState, 'insert-fragment');

            setEditorState(newEditorState);
          }
        }
      }
    },

    handleKeyCommand(command: any, editorState: any, eventTimeStamp: number, { setEditorState }: any): DraftHandleValue {
      const selectionState = editorState.getSelection();

      // if (selectionState.isCollapsed()) {
      const anchorKey = selectionState.getAnchorKey();
      const currentContent = editorState.getCurrentContent();
      const currentBlock = currentContent.getBlockForKey(anchorKey);
      const currentBlockKey = currentBlock.getKey();
      const currentBlockType = currentBlock.getType();
      //
      //
      //   return 'handled';
      // }
      // if (command === commands.UL || command === commands.OL) {
      //   // Set up the base types/checks
      //   let listType = blockTypes.UL;
      //   let listTest = text => UL_INTERCEPT_REGEX.test(text);
      //   if (command === commands.OL) {
      //     listType = blockTypes.OL;
      //     listTest = text => OL_INTERCEPT_REGEX.test(text);
      //   }
      //
      //   console.error(command);
      //
      //   let editorState = getEditorState();
      //   let content = editorState.getCurrentContent();
      //
      //   // Retrieve the focused block
      //   const selection = editorState.getSelection();
      //   const blockKey = selection.getStartKey();
      //   let block = content.getBlockForKey(blockKey);
      //
      //   // Check if it matches our criteria for creating a list: unstyled, at
      //   // the top-level and empty.
      //   const blockText = block.getText();
      //
      //   if (
      //     block.getType() === blockTypes.UNSTYLED &&
      //     block.getDepth() === 0 &&
      //     listTest(blockText)
      //   ) {
      //     // Convert the existing block to an unordered list
      //     editorState = RichUtils.toggleBlockType(editorState, listType);
      //     content = editorState.getCurrentContent();
      //     block = content.getBlockForKey(blockKey);
      //
      //     // Select the entire block
      //     const blockSelection = new SelectionState({
      //       anchorKey: blockKey,
      //       anchorOffset: 0,
      //       focusKey: blockKey,
      //       focusOffset: block.getLength(),
      //     });
      //
      //     // Replace with the text with either nothing (if we created a list)
      //     // or the existing content
      //     content = Modifier.replaceText(
      //       content,
      //       blockSelection,
      //       '',
      //     );
      //   } else {
      //     // We’ve intercepted the normal keyboard command propagation here, so
      //     // we needs to manually insert a space if we're not injecting a list
      //     content = Modifier.insertText(
      //       content,
      //       selection,
      //       ' ',
      //     );
      //   }
      //   // Propagate the new state up
      //   editorState = EditorState.push(editorState, content);
      //   editorState = EditorState.forceSelection(editorState, content.getSelectionAfter());
      //   setEditorState(editorState);
      //
      //   return 'handled';
      // }
      return 'not-handled';
    },
  };
}

