import { Modifier, Entity, EditorState } from 'draft-js';
import { PluginFunctions } from 'draft-js-plugins-editor';
// @ts-ignore
import { getSelectedBlock, checkSingleLinkEntry } from 'src/shared/utils/draftjs';
import linkifyIt from 'linkify-it';
import tlds from 'tlds';

import blockComponentCreator from 'src/Editor/plugins/link/components/BlockLink';
import entityComponentCreator from 'src/Editor/plugins/link/components/EntityLink';
import { ApplyModifier } from "src/Editor/index";
import { editorBlockLink } from "src/Editor/modifiers/editorBlockLink";

// import styles from './_styles.scss'; // eslint-disable-line no-unused-vars

const linkify = linkifyIt();

linkify.tlds(tlds);

function findLinkEntities(contentBlock: any, callback: any, contentState: any) {
  // console.log('fe', contentBlock.getText());
  contentBlock.findEntityRanges(
    (character: any) => {
      const entityKey = character.getEntity();
      return (
        entityKey !== null &&
        Entity.get(entityKey).getType() === 'LINK'
      );
    },
    callback,
  );
}

export default ({ applyModifier }: { applyModifier: ApplyModifier }) => {
  const BlockComponent = blockComponentCreator({});

  return {
    blockRendererFn: (contentBlock: any, { getProps }: { getProps: any }) => {
      if (contentBlock.getType() !== 'link') {
        return;
      }

      const {
        url = '',
        image,
        title,
        description,
      } = (contentBlock.data ? contentBlock.data.toObject() : {}) as any;

      return {
        isAtomic: true,
        editable: false,
        component: BlockComponent,
        props: {
          url,
          image,
          title,
          description,
          isAtomic: true,
        },
      };
    },

    onChange(editorState: any) {
      const contentBlock = getSelectedBlock(editorState);

      if (!contentBlock) return;

      const links = linkify.match(contentBlock.get('text'));

      let newState = editorState;

      // console.log('oc', contentBlock.get('text'));

      if (typeof links !== 'undefined' && links !== null) {
        let selection = newState.getSelection();

        links
          .filter((link: any) => contentBlock.getEntityAt(link.index) === null)
          .forEach((link: any) => {
            const entityKey = Entity.create('LINK', 'MUTABLE', { url: link.url });

            selection = selection.merge({
              anchorOffset: link.index,
              focusOffset: link.lastIndex,
            });

            let contentState = Modifier.replaceText(
              newState.getCurrentContent(),
              selection,
              `${link.url}`,
              newState.getCurrentInlineStyle(),
              entityKey,
            );

            newState = EditorState.push(newState, contentState, 'insert-characters');

            // insert a blank space after link

            selection = newState.getSelection().merge({
              anchorOffset: selection.get('anchorOffset') + link.url.length,
              focusOffset: selection.get('anchorOffset') + link.url.length,
            });

            newState = EditorState.acceptSelection(newState, selection);

            contentState = Modifier.insertText(
              newState.getCurrentContent(),
              selection,
              ' ',
              newState.getCurrentInlineStyle(),
              undefined,
            );

            newState = EditorState.push(newState, contentState, 'insert-characters');
          });
      }

      return newState;
    },


    handleKeyCommand(
      command: any,
      editorState: any,
      eventTimestamp: number,
      { getProps, setEditorState, getEditorState }: PluginFunctions,
    ) {
      const contentState = editorState.getCurrentContent();
      const selectionState = editorState.getSelection();

      const anchorKey = selectionState.getAnchorKey();

      const block = contentState.getBlockForKey(anchorKey);

      if (selectionState.isCollapsed() && command === 'split-block' && block.getType() === 'unstyled') {
        const linkEntry = checkSingleLinkEntry(editorState);

        if (linkEntry.isSingleEntry) {
          const afterSplit = Modifier.splitBlock(contentState, selectionState);
          const newEditorState = EditorState.push(editorState, afterSplit, 'split-block');

          applyModifier(editorBlockLink, { url: linkEntry.linkData.url });

          setEditorState(newEditorState);

          // const contentState = editorState.getCurrentContent();
          // const selectionState = editorState.getSelection();

          // const withNewBlockType = Modifier.setBlockType(currentContent, selectionState, 'link');
          // const withNewBlockData = Modifier
          //   .setBlockData(withNewBlockType, selectionState, linkEntityData);
          // const afterSplit = Modifier.splitBlock(withNewBlockData, selectionState);
          //
          // const editorStateAfterSplit = EditorState.push(editorState, afterSplit, 'split-block');
          // const selectionAfterSplit = editorStateAfterSplit.getSelection();
          //
          // const afterNewBlockType = Modifier.setBlockType(afterSplit, selectionAfterSplit, 'unstyled');
          //

          return 'handled';
        }
      }

      return 'not-handled';
    },

    decorators: [
      {
        strategy: findLinkEntities,
        component: entityComponentCreator({} as any),
      },
    ],
  };
};
