import { List } from 'immutable';

type KeysList = List<string>;

export type BlockKeyStore = {
  add: (key: string) => KeysList;
  remove: (key: string) => KeysList;
  includes: (key: string) => boolean;
  getAll: () => KeysList;
};

const createBlockKeyStore = (): BlockKeyStore => {
  let keys: KeysList = List();

  const add = (key: string) => {
    keys = keys.push(key);
    return keys;
  };

  const remove = (key: string) => {
    keys = keys.filter((item) => item !== key) as KeysList;
    return keys;
  };

  return {
    add,
    remove,
    includes: (key) => keys.includes(key),
    getAll: () => keys,
  };
};

export default createBlockKeyStore;
