import React, { Component } from 'react';
import cn from 'classnames';
// @ts-ignore
import styles from './style.css';

const getDelimiterComponent = ({ decorator }: any) => {
  const DelimiterComponent = (props: any) => {
    const { isFocused } = props.blockProps;
    const activeClass = '';

    return (
      <div
        className={cn(styles.wrapper, activeClass)}
      >
        <hr className={styles.delimiter} />
      </div>
    );
  };
  return decorator ? decorator(DelimiterComponent) : DelimiterComponent;
};

export default getDelimiterComponent;
